export default {
    data() {
        return {
            value:'',tabShow: 0, tab_list:[ '今日任务','今日完成' ],
            img:[require('../../assets/img/TheEarly.png'),require('../../assets/img/ThisNight.png') ],
        }
    },
    components: {
        
    },
    beforeCreate(){
        this.$store.dispatch('getMyTask', '0')
    },
    computed: {
        list(){
            return this.$store.state.list
        }
    },
    created(){
        document.title = '点检任务'
    },
    methods: {
        tab(index){
            this.tabShow = index
        },
        details(index){
            this.$router.push({path:'/Check'})
        }
    },
}



